import React from 'react'

const Item = (props) => {
    let run = () =>{
        let selectCoinID = props.dataItem.CoinInfo.Id;
        props.getCoinDetails(selectCoinID);
        props.isShowCoinDetails();
    }
    return (
        <li onClick={ ()=>run()}>
            {props.coinNumber}
            {props.dataItem.CoinInfo.Id}
            {props.dataItem.CoinInfo.Name}
            <img src={`https://www.cryptocompare.com${props.dataItem.CoinInfo.ImageUrl}`} />
        </li>
    )
}


export default Item